import { FC, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Route, Switch } from 'react-router';
// Components
import { Sidebar } from '../../Components/Sidebar/Sidebar';
// Thunks
import { loadOperatorThunk } from '../../Redux/Auth/AuthThunks';
// Routes
import {OperatorRoutes} from './routes';
// Styles
import styles from './Operator.module.css';


// Types
type RouteType = {
  path: string;
  Component: FC;
}


export default function OperatorIndex() {
  const dispatch = useDispatch();
  
  // Initial actions
  useEffect(() => {
    dispatch(loadOperatorThunk());
  }, [dispatch]);


    return (
      <>
          <div className={styles.main_block}>
            <Sidebar />
            <Switch>
              {OperatorRoutes.map(({path, Component}: RouteType) => <Route component={Component} exact key={path} path={path} />)}
            </Switch>
          </div>
      </>
    )
}