import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// Store
import { GetAccessToken } from '../../../Redux/Auth/AuthSelector';
import { GetEstates } from '../../../Redux/Operator/Unapproved/UnapprovedSelector';
// Components
import { EstateActivationModal } from '../../../Components/Modal/EstateActivationModal/EstateActivationModal';
// Types
import {estatesDataType} from '../../../Redux/Operator/Unapproved/UnapprovedReducer';
// Styles
import styles from './Unapproved.module.css';
// Images
import CachedIcon from '@mui/icons-material/Cached';
// Thunks
import { getEstateDataThunk, getEstatesDataThunk } from '../../../Redux/Operator/Unapproved/UnapprovedThunk';
import { Header } from '../../../Components/Header/Header';
import { Button } from '@mui/material';


export const Unapproved = () => {
    const dispatch = useDispatch();

    // Local state
    const [clicked_id, setClicked_id] = useState(0);
    const [activation_modal_shown, setActivation_modal_shown] = useState(false);
    const HeaderItems = ['ID', 'Цена', 'Локация', 'Название', 'Фотография', 'Активировать'];

    // Global state
    let estates = useSelector(GetEstates);
    const access_token = useSelector(GetAccessToken);

    // Functions
    const onButtonClick = (id: number) => {
        setClicked_id(id);
        setActivation_modal_shown(true);
    };

    const loadEstates = () => {
        dispatch(getEstatesDataThunk(access_token));
    };

    // Initial actions
    useEffect(() => {
        dispatch(getEstatesDataThunk(access_token));
    }, [access_token, dispatch]);

    const estatesData: estatesDataType = estates?.map((estate: any) => {
        return (
            <div className={styles.estates_block}>
                <div>{estate.id}</div>
                <div>{estate.price}</div>
                <div>{estate.location}</div>
                <div>{estate.real_estate_name}</div>
                <div>{estate.real_estate_name}</div>
                <Button variant='contained' onClick={() => onButtonClick(estate.id)}>Активировать</Button>
            </div>
        );
    });

    return (
        <div className='relative'>
            <CachedIcon onClick={loadEstates} className='absolute cursor-pointer text-blue-600 top-0 left-8' />
            <EstateActivationModal isModalOpen={activation_modal_shown} changeModalOpen={setActivation_modal_shown} id={clicked_id}/>
            
            <div className={styles.main_block}>

                <div className={styles.content_block}>
                    <Header headerItems={HeaderItems}/>
                    <div>
                        {estatesData}
                    </div>
                </div>
            </div>

        </div>
    );
};