import { AxiosResponse } from "axios";
import toast from "react-hot-toast";
import { api } from "../../../api";
import { EstateDataType, UnapproveActions } from "./UnapprovedReducer";

export const getEstatesDataThunk = (token: string) => {
    return async (dispatch: any) => {
        try {
            debugger;
            if (token === "") return;
            const response = await api.getApi({url: 'operator/get-confirm-real-estates?page=0&limit=10', token: token, withCredentials: true, contentType: false})
            debugger;
            dispatch(UnapproveActions.addEstatesData(response.data));
        } catch (error: any) {
            toast.error(error.response.message)
        }
    };
};

export const getEstateDataThunk = (token: string, id: number) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            const response: AxiosResponse<EstateDataType> = await api.getApi({url: `operator/get-real-estate/${id}`, token: token, withCredentials: true, contentType: false})
            await dispatch(UnapproveActions.addEstateData(response.data));
            return response;
        } catch (error: any) {
            toast.error(error.response.message)
        }
    };
};

export const sendEstateActivationThunk = (id: number, result: boolean, token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            await api.postApi({url: `activation-real-estate/${id}`, token: token, withCredentials: true, params: {bool: result}})
            await dispatch(UnapproveActions.addDeleteEstate(id));
        } catch (error: any) {
            toast.error(error.response.message)
        }
    }
} 


