import Modal from 'react-modal';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// Components
import { GetAccessToken } from '../../../Redux/Auth/AuthSelector';
import { Slider } from '../../Slider/Slider';
// State
import { getEstateDataThunk, sendEstateActivationThunk } from '../../../Redux/Operator/Unapproved/UnapprovedThunk';
// Styles
import styles from './EstateActivationModal.module.css';
import { Exit } from '../../Exit/Exit';
import { Button } from '@mui/material';
import { StateType } from '../../../Redux/store';
import { GetEstate } from '../../../Redux/Operator/Unapproved/UnapprovedSelector';
import { EstateDataType } from '../../../Redux/Operator/Unapproved/UnapprovedReducer';


export const EstateActivationModal = (props: PropsType) => {
    const dispatch = useDispatch();
    
    // Local store
    const access_token = useSelector(GetAccessToken);

    // Global state
    const estate: EstateDataType = useSelector(GetEstate);

    // Functions
    const onButtonClick = (boolValue: boolean) => {
        dispatch(sendEstateActivationThunk(props.id, boolValue, access_token));
        props.changeModalOpen(false);
    };

    // Initial actions
    useEffect(() => {
        dispatch(getEstateDataThunk(access_token, props.id));
    }, [access_token, dispatch, props.id]);

    return (
        <Modal portalClassName={styles.modal_portal} className={styles.main_block} isOpen={props.isModalOpen}>
            <div className={styles.content_block}>
                <Exit changeModalOpen={() => props.changeModalOpen(false)}/>
                <Slider images={estate.images}/>
                    <div className={styles.estateInfo}>
                        <div>Цена: {estate.price}</div>
                        <div>Дата создания: {estate.created_at?.substr(0, 10)}</div>
                        <div>Телефон: {estate.phone}</div>
                        <div>Площадь: {estate.area}</div>
                        <div>Локация: {estate.location}</div>
                        <div>Название: {estate.real_estate_name}</div>
                        <div>Тип собственника: {estate.owner_type}</div>
                        <div>Вип: {estate.vip ? 'Да': 'Нет'}</div>
                    </div>
                    <div className={styles.button_block}>
                        <Button variant='contained' onClick={() => onButtonClick(true)}>Rugsat bermek</Button>
                        <Button color='error' variant='contained' onClick={() => onButtonClick(false)}>Yzyna gaýtarmak</Button>
                    </div>
            </div>
        </Modal>
    );
}

type PropsType = {
    id: number;
    changeModalOpen: (bool: boolean) => void;
    isModalOpen: boolean;
};