import { useState } from 'react';
import styles from './Slider.module.css';
import rightArrow from '../../Images/right-arrow.png';
import { env } from 'process';

export const Slider: React.FC<PropsType> = (props) => {
    const [image_number, setImage_number] = useState(0);
    
    return (
        <div className={styles.main_block}>
            {props.images
                ? 
                    <div className={`${styles.image_block} relative top-0 left-0`}>
                        <button disabled={(image_number === 0)} className={`${styles.arrow_image} ${styles.arrow_image_left} transform rotate-180`} onClick={() => setImage_number(image_number - 1)}>
                            <img alt='Left arrow' src={rightArrow}/>
                        </button>
                        <img alt='Main' className={styles.main_image} src={`${process.env.REACT_APP_BASE_URL}/${props.images[image_number].destination}-large.webp`}/>
                        <button className={`${styles.arrow_image} ${styles.arrow_image_right}`} disabled={(image_number === (props.images.length - 1))} onClick={() => setImage_number(image_number + 1)}>
                            <img alt='Right arrow' src={rightArrow} />
                        </button>
                        <p className='text-center text-black'>{image_number + 1} / {props.images.length}</p>
                    </div>
                : <div className='text-center font-bold'>Фотографий нету</div>
            }
            
        </div>
    );
}

// Types
type PropsType = {
    images: null | {destination: string}[];
};